.application {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;

  .title-section {
    flex: 0 0 110px;
    .title {
      padding: 33px 0 33px 33px;
      font-size: 36px;
    }
  }

  .application-section {
    flex: 1;
    display: flex;
    flex-direction: column;

    .application-navigation {
      display: flex;

      div {
        font-size: 24px;
        width: 50%;
        height: 53px;

        display: flex;
        align-items: center;
        justify-content: center;

        &:hover {
          background-color: #fafafa;
          cursor: pointer;
        }
      }

      div.active {
        background-color: #eee;
      }
    }

    .application-body {
      flex: 1;
      padding: 33px;
      background-color: #eee;
      overflow-y: auto;

      display: flex;
      flex-direction: column;
    }
  }
}
